* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "TiemposFine-Bold";
  font-size: 10px;
}
@media (max-width: 1024px) {
  html {
    font-size: 8px;
  }
}

main {
  background-color: #E5E5E5;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.wrapper {
  position: relative;
  width: 81.7%;
  max-height: 70rem;
}
.wrapper__video-block {
  background: url("/public/images/mask.png") bottom center no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 70rem;
  z-index: 0;
}
.wrapper:after {
  content: "";
  background: url("/public/images/video-block.png") bottom center no-repeat;
  background-size: cover;
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: center;
  align-items: center;
  margin: 12rem 0;
  padding: 8rem 0;
  width: 80rem;
}
.content__rectangle {
  width: 10rem;
  height: 0.9rem;
  background-color: #FFDF37;
  z-index: 2;
}
.content__title-common {
  line-height: 6rem;
  color: #FFFFFF;
  font-weight: 400;
  position: relative;
  z-index: 2;
  letter-spacing: 0.027rem;
  font-size: 5rem;
}
.content__title-middle {
  line-height: 6rem;
  color: #FFFFFF;
  font-weight: 400;
  position: relative;
  z-index: 2;
  font-family: "Quentin";
  letter-spacing: 0.038rem;
  font-size: 7rem;
  padding: 0 2rem;
}
.content__btn-play {
  background: url("/public/images/play.png") bottom center no-repeat;
  width: 4.1rem;
  height: 3rem;
  outline: none;
  border: none;
  position: relative;
  z-index: 2;
}
.content__btn-play:active {
  border: 0.1rem solid #3a3A3a;
  border-radius: 0.8rem;
}

.wrapper__title {
  color: #fff;
  font-family: "Lobster", cursive;
  font-size: 1.9rem;
  z-index: 2;
  line-height: 3rem;
  margin: 0 20%;
  text-align: center;
}